
import { key } from '@/store'
import { computed, defineComponent } from 'vue'
import { useStore } from 'vuex'
import { KeysMutations } from '@/store/keys/mutations'
import { CompanyActions } from '@/store/company/actions'
import { AuthActions } from '@/store/auth/actions'

export default defineComponent({
	props: {
		modelValue: {
			type: Number,
			reqired: true
		}
	},
	emits: ['update:modelValue'],
	setup(){
		const store = useStore(key);
		const isDebug = computed<boolean>({
			get: () => store.getters.getIsDebug,
			set: (val: boolean) => store.commit(KeysMutations.SET_IS_DEBUG, val)
		})
		const menu = computed(() => {
			let menu_start = [
				{title: ' ', link: null, lock: false, children: [
				]},
				{title: ' ', link: null, lock: false, children: [
					{title: ' ', link: '/product', lock: false},
					{title: '', link: '/orders', lock: false},
					{title: '', link: '/shipments', lock: false},
					{title: '', link:'/claims', lock: false},
					{title: ' ', link: '/kp', lock: true},
					{title: '', link: '/history', lock: true},
					
				]},
				{title: '', link: '/settlements', lock: false, children: [
					{title: '  ', link: '/shipments/request', lock: false},
					{title: ' ', link: '/shipments/address', lock: false},
				]},
				{title: '', link: null, lock: true, children: [
					{title: '', link: '/permissive', lock: true},
					{title: '', link: '/regulatory', lock: true},
					{title: '.', link: '/dop_info', lock: true},
				]},
				{title: '', link: '/settlements', lock: true, children: [
					{title: '', link: '/bills', lock: true},
					{title: '', link: '/realization', lock: true},
					{title: '', link: '/adjustments', lock: true},
					{title: ' ', link: '/acts', lock: true},
					{title: '', link: '/edo', lock: true},
				]},
				{title: '', link: null, lock: false, children: [
					{title: '', link: '/catalog', lock: false},
					{title: '', link: '/dop_info', lock: true},
				]},
				{title: '', link: null, lock: true, children: [
					{title: '', link: '/banners', lock: true},
					{title: ' ', link: '/photo_mat', lock: true},
					{title: ' ', link: '/ed_mat', lock: true},
					{title: '', link: '/ingration', lock: true},
				]},
				{title: 'Novelty', link: '/Novelty', lock: true, children: null },
				{title: '', link: '/contacts', lock: true, children: null},
				{title: 'Help', link: '/help', lock: true, children: null},
				{title: '', link: '/logout', lock: false, children: null},
			]
			let arr = [];
			if (!store.getters.isCompanysLoad) {
				store.dispatch(CompanyActions.GET_COMPANYS);
			}
			store.getters.getCompanys.forEach((element: { name: string; uid: string; }) => {
				arr.push({title: element.name
											.replace(/   /, '')
											.replace(/ /, ''),
						link: '/company/'+element.uid,
						lock: false,
						})	
			});
			arr.push({title: '', link: '/agreements', lock: true});
			arr.push({title: '', link: '/analytics', lock: true});
			arr.push({title: ' ', link: '/loyalty', lock: true});
			menu_start[0].children = arr;
			return menu_start;
		});
		return {
			menu,
			isDebug,
			logout: ()=>store.dispatch(AuthActions.LOGOUT),
		}
	}
})
